.skills {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #171717;
  position: relative;

  &__container {
    flex-direction: column;
    min-height: 100vh;
    padding: 80px 0 0 0;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
}
